.animation_container {
  display: inline-block;
  position: relative;
  width: 100px;
  height: 70px;
}

.animation_container canvas {
  position: absolute;
  display: block;
  width: 100px;
  height: 70px;
}

.animation_container .dom_overlay_container {
  pointer-events: none;
  overflow: hidden;
  width: 100px;
  height: 70px;
  position: absolute;
  left: 0;
  top: 0;
  display: block;
}

